.chat {
  position: fixed;
  bottom: 80px;
  right: 20px;
  background-color: white;
  width: 500px;
  height: 400px;
  border-radius: 3%;
  color: black;
}

.chat-title {
  padding-left: 50px;
  padding-top: 20px;
}

.close-chat {
  padding-right: 40px !important;
  padding-top: 20px !important;
}

.start-chat {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  text-transform: uppercase;
  background-color: darkred;
  border-color: darkred;
  border-style: none;
}

.start-chat:focus {
  outline:none;
}

.messages-container {
  padding-left: 15px;
}

.messages-container {
  height: 240px;
  width: 450px;
  border-radius: 10%;
  overflow: auto;
  background-color: white;
}

.inputs-chat {
  display: flex;
  padding-left: 20px;
}

.inputs-chat input {
  width: 350px;
}

.inputs-chat button {
  background-color: black;
  border-color: black;
}

.users-container-chat {
  padding-top: 50px;
  padding-left: 50px;
  height: 300px;
  width: 450px;
  border-collapse: black;
}

.user-chat {
  display: flex;
  align-items: center;
  padding: 5px;
  border-bottom: 1px solid black;
  width: 350px;
}

.user-chat:hover {
  background-color: black;
  color: white;
}

.user-select-chat {
  margin-bottom: 0;   
  margin-left: 10px;
}

.img-chat {
  width: 30px;
  border-radius: 50%;
}